<template>
  <div class="tab">
    <router-link tag="div" class="tab-item" to="/recommend">
        <span class="tab-link">新闻</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/singer">
      <span class="tab-link">娱乐</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/rank">
      <span class="tab-link">体育</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/search">
      <span class="tab-link">图片</span>
    </router-link>
  </div>
</template>
<script>
    export default {}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .tab
    display: flex
    height: 44px
    line-height: 44px
    font-size: 18px
    .tab-item
      flex: 1
      text-align: center
      background:#f33
      .tab-link
        padding-bottom: 5px
        color: #fff
      &.router-link-active
        .tab-link
          color: #000
         /* border-bottom: 2px solid $color-theme*/
</style>





