<template>
  <div class="recommend">
    <scroll class="recommend-content" :data="dissList">
      <div>
      	<div class="slider-wrapper" v-if="dissLists.length">
          <slider>
            <div class="titles" v-for="item in dissLists" v-if="item.picInfo[0]?item.picInfo[0].url:''">
              <a :href="item.link">
                <img :src="item.picInfo[0]?item.picInfo[0].url:''">
                <span class="title" v-html="item.title"></span>
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <ul>
            <li v-for="item in dissList" class="item">
              <a :href="item.link" class="a">
              	<div class="icon" v-if="item.picInfo[0]?item.picInfo[0].url:''">
              		<img v-lazy="item.picInfo[0]?item.picInfo[0].url:''" width="102" height="78">
	              </div>
	              <div class="text"">	
	                <h2 class="name" v-html="item.title"></h2>
	                <div class="de">
	                	<span class="desc" v-html="item.category"></span>
	                	<span class="des" v-html="item.tcount"></span>
	                </div>
	              </div>
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!dissList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>
<script>
    import axios from "axios"
    import Slider from "base/slider/slider"
    import Scroll from "base/scroll/scroll"
    import Loading from "base/loading/loading"

    export default {
      data(){
        return {
          dissList:[],
          dissLists:[]
        }
      },
      components:{
        Slider,
        Scroll,
        Loading
      },
      created(){
      	this._getDissList();
      },
      methods:{
        _getDissList(){
          axios.get('api/getDissList').then((res)=>{
          		this.dissLists=res.data.focus;
              this.dissList=res.data.list;
          })
        }
      }
    }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

	.recommend
	  position: fixed
	  width: 100%
	  top: 88px
	  bottom: 0
	  .recommend-content
	    height: 100%
	    overflow: hidden
	    .slider-wrapper
	      position: relative
	      width: 100%
	      overflow: hidden
	      .titles
	      	position:relative
	      	.title
	      		position:absolute
	      		bottom:10px
	      		left:0
	      		color:#fff
	      		font-size:18px
	    .recommend-list
	      .item
	        display: flex
	        box-sizing: border-box
	        align-items: center
	        padding: 10px 0
	        margin:0 15px 0 15px 
	        border-bottom: 1px solid #e5e5e5
	        .a
	          text-decoration: none
	          display: inline-flex
		        .icon
		          flex: 0 0 60px
		          width: 60px
		          padding-right: 55px
		        .text
		          display: flex
		          flex-direction: column
		          justify-content: center
		          flex: 1
		          line-height: 20px
		          overflow: hidden
		          .name
		            margin-bottom: 10px
		            color: #404040
		            font-size: 18px
		            font-weight: normal
		          .desc
		            color: #888
		            float:left
		          .des
		            color: #888
		            text-algin:right
		            float:right
		    .loading-container
		        position: absolute
		        width: 100%
		        top: 50%
		        transform: translateY(-50%)
</style>





