<template>
  <div class="m-header">
  	<i class="icon-dl"></i>
    <div class="icon"></div>
    <h1 class="text"></h1>
    <router-link class="mine"  to="/user">
      <i class="icon-xiazai"></i>
      <i class="icon-youxiang"></i>
    </router-link>
  </div>
</template>
<script>
    export default {}
</script>
<style scoped lang="stylus">
  @import "~common/stylus/variable";
  @import "~common/stylus/mixin"

  .m-header
    position: relative
    height: 44px
    text-align: center
    color: $color-theme
    .icon-dl
      display: block
      padding: 6px
      font-size: 28px
      color: $color-theme
      float:left	
    .icon
      display: inline-block
      vertical-align: top
      margin-top: 8px
      width: 132px
      height: 27px
      margin-right: 40px
      background:url('logo_2017_v1.png')no-repeat
      background-size: 132px 27px
    .text
      display: inline-block
      vertical-align: top
      line-height: 44px
      font-size: $font-size-large
    .mine
      position: absolute
      top: 0
      right: 0
      .icon-xiazai
        display: block
        padding: 12px
        font-size: 20px
        color: $color-theme
        float:left
      .icon-youxiang
       	display: block
        padding: 12px
        font-size: 20px
        color: $color-theme
        float:left
      
</style>




