<template>
  <div class="recommend">
    <scroll class="recommend-content" :data="dissList">
      <div>
      	<div class="slider-wrapper" v-if="dissLists.length">
          <slider>
            <div class="titles" v-for="item in dissLists" v-if="item.picInfo[0]?item.picInfo[0].url:''">
              <a :href="item.link">
                <img :src="item.picInfo[0]?item.picInfo[0].url:''">
                <span class="title" v-html="item.title"></span>
              </a>
            </div>
          </slider>
        </div>
        <div class="recommend-list">
          <ul>
            <li v-for="item in dissList" class="item">
              <a :href="item.link" class="a">
              	<div class="icon" v-if="item.picInfo[0]?item.picInfo[0].url:''">
              		<img v-lazy="item.picInfo[0]?item.picInfo[0].url:''" width="185" height="236">
	              </div>
	              <div class="text"">	
	                <h2 class="name" v-html="item.title"></h2>
	                <div class="de">
	                	<span class="desc" v-html="item.category"></span>
	                	<span class="des" v-html="item.tcount"></span>
	                </div>
	              </div>
              </a>
            </li>
          </ul>
        </div>
      </div>
      <div class="loading-container" v-show="!dissList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>
<script>
    import axios from "axios"
    import Slider from "base/slider/slider"
    import Scroll from "base/scroll/scroll"
    import Loading from "base/loading/loading"

    export default {
      data(){
        return {
          dissList:[]
        }
      },
      components:{
        Slider,
        Scroll,
        Loading
      },
      created(){
      	this._getDissList();
      },
      methods:{
        _getDissList(){
          axios.get('api/getDissList').then((res)=>{
              this.dissList=res.data.live;
	          	this.dissLists=res.data.news;
          })
        }
      }
    }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

.recommend{
	 position: fixed
	  width: 100%
	  top: 88px
	  bottom: 0
}
	 
.recommend-content{
	 height: 100%
	 overflow: hidden
}
	   
.slider-wrapper{
	position: relative
	width: 100%
	overflow: hidden
}     
.titles{
	position:relative
}      	
.title{
	position:absolute
	bottom:20px
	left:0
	color:#fff
	font-size:18px
}
.item{
	width: 185px;
	height: 336px;	
	display: inline-flex;
	background: #fff;
	margin: 10px;
	box-shadow: 0 1px 1px #e3e3e3;
	font-size: 18px;
}
.icon img{
	float: left;
	overflow: hidden;
	position: relative;
}
.text{
	padding: 15px;
	float: left;
	box-sizing: border-box;
}
.name{
	color: #555;
	margin-bottom: 15px;
	font-size: 15px;
}
.desc{
	float: left;
	line-height: 30px;
	font-size: 15px;
}    
.des{
	float: right;
	line-height: 30px;
	font-size: 18px;
}

    
.loading-container{
	position: absolute;
	width: 100%;
	top: 50%;
	transform: translateY(-50%);
}
		        
</style>





